import React, { useState, useEffect } from "react";

import JobService from "../../service/JobService"; 
import PaymentAdmin from "./PaymentAdmin";
import { Formik, Form } from "formik";
import { Grid,Button,Paper,Typography } from "@material-ui/core";
import AppDatePicker from "../../lib/components/datepicker/appDatePicker";
import { ThemeProvider } from "@material-ui/core/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import moment from 'moment';

const todayDate=moment(new Date());
const yesterdayDate=moment(new Date()).subtract(1, 'days');


const searchForm = {
  fromDate: yesterdayDate.format('DD/MM/YYYY'),
  toDate: todayDate.format('DD/MM/YYYY'),
 
};

  const Theme = createMuiTheme({
    overrides: {
      MuiOutlinedInput: {
        root: {
          // position: "relative",
          // "& $notchedOutline": {
          //   borderColor: "#2EFF22"
          // },
          "&:hover:not($disabled):not($focused):not($error) $notchedOutline": {
            borderColor: "#0E1B3D",
            // Reset on touch devices, it doesn't add specificity
            "@media (hover: none)": {
              borderColor: "#0E1B3D",
            },
          },
          "&$focused $notchedOutline": {
            borderColor: "#0E1B3D",
            borderWidth: 1.5,
          },
        },
      },
    },
  });

  
 



function PaymentMain() {
  const [jobs, setJobs] = useState([]);
  const [formvalues] = useState(searchForm);
  const[reRender,setRerender]=useState(0);
  const [selectedFromDate,setSelectedFromDate]=useState();
  const [selectedToDate,setSelectedToDate]=useState();

 
  

  const onSearch = (values) => {

    setSelectedFromDate(values.fromDate);
    setSelectedToDate(values.toDate);
    JobService.fetchInvoiceApprovedJobs(values.fromDate,values.toDate)
    .then((response) => {
        if (response.isAxiosError) 
         setJobs([]);
        else{   
        setJobs(response.data.dataItems);
        }
      
      })
      .catch(() => {
        console.log("error");
      });
  };



 

  useEffect(() => {
    const loadInvoiceApprovedJobs = async () => {
      setJobs([]);
      let fromDate;
      let todate;
      if(selectedFromDate || selectedToDate)
      {
         fromDate=selectedFromDate;
         todate=selectedToDate;
      }
      else
      {
       fromDate=yesterdayDate.format('DD/MM/YYYY');
       todate=todayDate.format('DD/MM/YYYY');
      }
      JobService.fetchInvoiceApprovedJobs(fromDate,todate)
        .then((response) => {
          if (response.isAxiosError) 
           setJobs([]);
          else{
        
          setJobs(response.data.dataItems);
          }
        
        })
        .catch(() => {
          console.log("error");
        });
    };

    loadInvoiceApprovedJobs();
  }, [reRender]);


  const [] = React.useState(false);

  return (
    <>
     <Formik initialValues={formvalues} enableReinitialize>
          {(formik) => {
            
            return (
              <>
                <Form autoComplete="off">
                  <ThemeProvider theme={Theme}>
                    <Grid container spacing={1}>
                      <Grid item xs={2}>
                        <AppDatePicker
                          name={"fromDate"}
                          id={"fromDate"}
                          label={"From Date"}
                          iconColor="#0E1B3D"
                          defaultDate={yesterdayDate}
                        />
                      </Grid>
                      <Grid item xs={2}>
                        <AppDatePicker
                          name={"toDate"}
                          id={"toDate"}
                          label={"To Date"}
                          iconColor="#0E1B3D"
                          defaultDate={todayDate}
                        />
                      </Grid>
                     

                      <Grid item xs={3}>
                        <Button
                          variant="contained"
                          style={{
                            backgroundColor: "#0E1B3D",
                            color: "#FFFFFF",
                            marginTop: "7px",
                          }}
                          onClick={() => onSearch(formik.values)}
                        >
                          Search
                        </Button>
                      </Grid>
                    </Grid>
                  </ThemeProvider>
                </Form>
              </>
            );
          }}
        </Formik>

      {jobs && jobs.length>0?
       (jobs.map((job,indx)  => (
          <PaymentAdmin
           job={job}
            key={job.referenceNumber}
            index={indx}
            pageRerender={() => setRerender(reRender+1)}
         
          ></PaymentAdmin>
        ))):(<Paper elevation={5} style={{ borderRadius: 8, padding: '30px', marginTop: 20, minWidth: '760px', minHeight: '100px', color: '#FF7171' }}>
        <Grid container direction="row" spacing={5}>
          <Grid item sm={12} xs={12}>
            <Typography variant="subtitle1" style={{ fontSize: '16px', textAlign: 'center' }}>
              <b>{'No records found'}</b></Typography>
          </Grid>
        </Grid>
      </Paper>)}

       
       
    </>
  );
}

export default React.memo(PaymentMain);
