import { validate } from "@material-ui/pickers";
import React, { useEffect, useState } from "react";
import AssignTokenService from "./AssignTokenService";
import VerifyPodCard from "./VerifyPodCard";
import VerifyInvoicesMain from "./VerifyInvoicesMain";
import PaymentsMain from "./PaymentsMain";
import CustomTabs from "../../lib/components/tabs/CustomTabs";
import ErrorToast from "../../lib/components/toast/ErrorToast";
import SuccessToast from "../../lib/components/toast/SuccessToast";


function VerifyDocument() {

    const [jobs, setJobs] = useState([]);
    const [render, setRender] = useState(0);
    const [tabSelected, setTabSelected] = useState("VERIFY POD");
    const [selectedRows, setSelectedRows] = useState([]);
    const [pstate, setPstate] = useState(0);
  const [rerender, setRerender] = useState(0);
  const [showToaster, setShowToaster] = useState(null);

    const [tabLabels, setTabLabels] = useState([
        "VERIFY POD",
        "VERIFY INVOICES",
        "PAYMENTS"
       
    ]);
    
    useEffect(() => {
        const loadPendingPODJobs = async () => {
            AssignTokenService.fetchPendingPODJobs()
              .then((response) => {
                if (response.isAxiosError)
                  setJobs([]);
                else {
                  console.log("response in ::", response);
                  setJobs(response);
                  console.log("length", response.length);
                }
            })
            .catch((error) => {
              console.log("error");
            });
        };
    
        loadPendingPODJobs();
    }, [rerender]);

    const updateJobList = (job) => {
        job.open = !job.open;
        setRender(render + 1);
    }

    const validate = (job) => {
        var count = job.requestContainerList.length;
        job.requestContainerList.map((container, inx) => {
            if (container.token === "" || container.token===null || container.token===undefined) {
                count = count + 1;
            }
            else {
                count = count -1;
            }
        })
            if (count === 0)
                job.disabled = false;
            setRender(render + 1);
        
  }
  
  const reRender = () => {
    setRerender(rerender + 1);
  }
    
    return (
        <>
            
      <div className="row">
          <div className="col-md-12">
          <h2 style={{color:"#C62926",textAlign:"left"}}>Verify Document</h2>
          <CustomTabs
              labelList={tabLabels}
             
              onSelected={(e) => {
                console.log("selected", e);
                if (e === 0) {
               
                  setTabSelected("VERIFY POD");
                  setSelectedRows([]);
                  setPstate(pstate + 1);
                }
                if (e === 1) {
                    setTabSelected("VERIFY INVOICES");
                    setSelectedRows([]);
                    setPstate(pstate + 1);
                }
                if (e === 2) {
                  setTabSelected("PAYMENTS");
                  setSelectedRows([]);
                  setPstate(pstate + 1);
              }
                
              }}
          ></CustomTabs>
        </div>
            </div>  
            <div className="row">
          <div className="col-md-12">
          {tabSelected === "VERIFY POD"  && <VerifyPodCard jobs={jobs}
                onExpandClicked={(job) => { updateJobList(job) }}
            //  validate={(job) => { validate(job) }}
              reRender={reRender}
              onApproveReject={(e) => {
              
                setShowToaster(e);
              }}
            ></VerifyPodCard> }
          {tabSelected === "VERIFY INVOICES"  && <VerifyInvoicesMain jobs={jobs}
               
             
            ></VerifyInvoicesMain> }

         {tabSelected === "PAYMENTS"  && <PaymentsMain jobs={jobs}
              
             
            ></PaymentsMain> }
          </div>
          
          {showToaster==='APPROVE' && 
          <SuccessToast
          icon="check_circle"
          title="POD verified successfully"
          message="*Item moved to delivered status with transporter"
          showToast={()=>{setShowToaster(false)}}
          position="top-right"
        />}

{showToaster==='REJECT' && 
          <ErrorToast
          icon="error" 
          title="POD rejected!"
          message="*Item sent back to transporter for reupload"
          showToast={()=>{setShowToaster(false)}}
          position="top-right"
        />}
            </div>  
            </>
    )
    
}
export default React.memo(VerifyDocument);